/* ---------- IMPORT GLOBAL STYLES ---------- */

@use "../globals.scss" as globals;

/* ---------- MOBILE-FIRST (max-width: 480px) ---------- */

.bannerContainer {  
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 89%;
  max-width: 1240px;
  height: 111px;
  margin-top: 16px;
  margin-bottom: 22px;
  background-color: globals.$quaternary-color;
  border-radius: globals.$mobile-radius;
  position: relative;
}

/* ---------- IPAD, TABLETS (min-width: 481px / max-width: 768px) ---------- */

@media screen and (min-width: 481px) {
    .bannerContainer {
    height: 111px;
    }
}

/* ---------- SMALL SCREENS, LAPTOPS (min-width: 769px / max-width: 1024px) ---------- */

/* ---------- DESKTOP, LARGE SCREENS (min-width: 1025px / max-width: 1200px) ---------- */

@media screen and (min-width: 1025px) {
    .bannerContainer {
    height: 223px;
    border-radius: globals.$desktop-radius;
    }
}

/* ---------- EXTRA LARGE SCREENS (min-width: 1201px) ---------- */