/* ---------- DESKTOP, LARGE SCREENS (min-width: 1025px / max-width: 1200px) ---------- */


@media screen and (min-width: 1025px) {
    .collapsesContainer {
        width: 90%;
        max-width: 1240px;
        margin: 0 auto;
        display: flex;
        column-gap: 8%;
        .collapse {
            width: 100%;
            p {
                font-size: 18px;
            }
        }    
    }
}

